:root {
  --white: #fff;
  --black: #000;
  --black-25: rgba(0,0,0, .25);
  --black-5: rgba(0,0,0, .5);
  --grey: #f5f5f5;
  --grey-2: #c4c4c4;
  --grey-3: #e1e1e1;
  --dark-grey: #686868;
  --red: #ed165f;
  --orange: #ff5318;
  --orange-light: #F07B4E;
  --green: #08B727;
  --shadow: 0 0 50px rgba(0,0,0, .1);
  --shadow-2: 0 0 5px 1px rgba(0,0,0, .1);
  --main-font: "Mulish", sans-serif;
  --sub-font: "Roboto", sans-serif;
}

html {
  color: var(--black);
  font-family: var(--main-font);
  font-size: 16px;
  line-height: 1.2;
  font-weight: 400;
  height: 100%;
  scroll-behavior: smooth;
}

body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  min-height: 100%;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  color: inherit;
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-top: 0;
}

input {
  appearance: none;
}

input:disabled + label {
  opacity: 0.4;
}

input[type=number] {
  -moz-appearance: textfield;
}

input[type=number]::-webkit-outer-spin-button,
input[type=number]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

main {
  position: relative;
}

.uppercase {
  text-transform: uppercase;
}

.overflow-hidden {
  overflow: hidden;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.header {
  background-color: var(--grey);
}

.header__container {
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1150px;
  padding-top: 15px;
  padding-bottom: 15px;
  box-sizing: content-box;
  position: relative;
}
@media (min-width: 768px) {
  .header__container {
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media (min-width: 1440px) {
  .header__container {
    padding-left: 50px;
    padding-right: 50px;
  }
}

.header__wrap {
  display: flex;
  align-items: center;
  margin-left: auto;
}
@media (min-width: 992px) {
  .header__wrap {
    order: 3;
    margin-left: initial;
  }
}

@media (min-width: 992px) {
  .header__search {
    margin-right: 40px;
  }
}

@media (min-width: 992px) {
  .logo {
    margin-left: 20px;
    margin-right: 20px;
    order: 2;
  }
}
@media (min-width: 1440px) {
  .logo {
    margin-left: 50px;
    margin-right: 50px;
  }
}

.nav--active {
  display: flex;
  flex-direction: column;
}
.nav--active .nav__list {
  transform: translateX(0);
}
@media (max-width: 991px) {
  .nav {
    order: 3;
  }
}
@media (min-width: 992px) {
  .nav {
    order: 1;
  }
}

.nav__toggle {
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 16px;
  height: 16px;
  position: relative;
}
.nav__toggle:hover {
  color: var(--red);
}
@media (min-width: 992px) {
  .nav__toggle {
    display: none;
  }
}

.nav__list {
  list-style: none;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
@media (max-width: 991px) {
  .nav__list {
    flex-direction: column;
    align-items: stretch;
    width: 100vw;
    min-height: 100vh;
    padding-left: 15px;
    padding-right: 15px;
    padding-bottom: 15px;
    background-color: var(--grey);
    box-sizing: border-box;
    overflow: auto;
    position: absolute;
    top: 100%;
    left: 0;
    transform: translateX(-100%);
    transition: 0.4s ease-out;
    z-index: 99;
  }
}

@media (min-width: 992px) {
  .nav__item {
    margin-right: 20px;
  }
  .nav__item:last-child {
    margin-right: 0;
  }
}
@media (min-width: 1440px) {
  .nav__item {
    margin-right: 50px;
  }
}

.nav__link {
  font-family: var(--main-font);
  font-weight: 600;
  letter-spacing: 0.2px;
  padding: 0 10px 10px;
  position: relative;
  transition: 0.2s;
}
.nav__link::before {
  content: "";
  width: 0;
  height: 1px;
  background-color: var(--red);
  transition: 0.3s;
  position: absolute;
  left: 50%;
  bottom: 0;
}
.nav__link:hover, .nav__link:focus {
  color: var(--red);
}
.nav__link:hover::before, .nav__link:focus::before {
  width: 100%;
  left: 0;
}
.nav__link--active {
  color: var(--red);
  font-weight: 800;
}
@media (max-width: 991px) {
  .nav__link {
    display: block;
    padding-top: 10px;
    padding-bottom: 10px;
  }
}
@media (min-width: 992px) {
  .nav__link--active::before {
    width: 100%;
    left: 0;
  }
}

.hamburger {
  transition: transform 400ms;
  user-select: none;
  pointer-events: none;
  width: 38px;
  position: absolute;
}
.hamburger--active {
  transform: rotate(45deg);
}
.hamburger--active .hamburger__line--top {
  stroke-dashoffset: -64px;
}
.hamburger--active .hamburger__line--middle {
  transform: rotate(90deg);
}
.hamburger--active .hamburger__line--bottom {
  stroke-dashoffset: -64px;
}

.hamburger__line {
  fill: none;
  transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
  stroke: currentColor;
  stroke-width: 8;
  stroke-linecap: round;
}
.hamburger__line--top {
  stroke-dasharray: 40 160;
}
.hamburger__line--middle {
  stroke-dasharray: 40 142;
  transform-origin: 50%;
  transition: transform 400ms;
}
.hamburger__line--bottom {
  stroke-dasharray: 40 85;
  transform-origin: 50%;
  transition: transform 400ms, stroke-dashoffset 400ms;
}

.search {
  display: flex;
  align-items: flex-end;
  position: relative;
}

.search__input {
  width: 175px;
  padding: 0 5px 3px 0;
  background: none;
  border: none;
  border-bottom: 1px solid var(--black-5);
  outline: none;
  position: relative;
}
.search__input:hover, .search__input:focus {
  border-bottom-color: var(--red);
}
@media (max-width: 767px) {
  .search__input {
    width: 0;
    padding: 0;
  }
}

.search__submit {
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
  color: var(--black);
  transition: 0.2s;
}
.search__submit:hover, .search__submit:focus {
  color: var(--red);
}

.user-nav {
  display: flex;
  margin: 0 20px;
}
@media (min-width: 768px) {
  .user-nav {
    margin: 0 30px;
  }
}
@media (min-width: 992px) {
  .user-nav {
    margin: 0;
  }
}

.user-nav__link {
  display: block;
  color: var(--black);
  width: 16px;
  height: 16px;
  margin-right: 30px;
  transition: 0.2s;
}
.user-nav__link:last-child {
  margin-right: 0;
}
.user-nav__link:hover, .user-nav__link:focus {
  color: var(--red);
}
.user-nav__link--active {
  color: var(--red);
}
@media (max-width: 767px) {
  .user-nav__link {
    margin-right: 20px;
  }
}

.user-nav__icon {
  width: inherit;
  height: inherit;
}

.button {
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
  color: var(--white);
  font-weight: 600;
  padding: 5px 15px;
  background-color: var(--red);
  border-radius: 2px;
  transition: 0.2s;
}
.button:hover, .button:focus {
  background-color: var(--orange);
}

.select {
  display: inline-block;
  letter-spacing: 0.2px;
  position: relative;
}
.select--active .select__caption {
  border-bottom-color: transparent;
}
.select--active .select__caption::after {
  display: block;
}
.select--active .select__list {
  transform: scale(1);
}

.select__caption {
  font-weight: 600;
  padding: 10px 15px;
  min-width: 200px;
  border: 1px solid var(--black);
  box-sizing: border-box;
  cursor: pointer;
  position: relative;
}
.select__caption::after {
  display: none;
  content: "";
  width: calc(100% - 8px);
  height: 1px;
  background-color: var(--black-25);
  position: absolute;
  left: 4px;
  bottom: 0;
  z-index: 3;
}

.select__list {
  list-style: none;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  width: 100%;
  padding-top: 5px;
  padding-bottom: 10px;
  background-color: var(--white);
  border: 1px solid var(--black);
  border-top: none;
  box-sizing: border-box;
  position: absolute;
  top: calc(100% - 1px);
  left: 0;
  transition: transform 0.3s cubic-bezier(0.77, 0, 0.175, 1);
  transform: scaleY(0);
  transform-origin: 0 0;
  z-index: 2;
}

.select__item {
  color: var(--black);
  padding: 10px 25px;
  background-color: var(--white);
  cursor: pointer;
  transition: 0.2s;
}
.select__item--active {
  color: var(--white);
  font-weight: 800;
  background-color: var(--red);
}
.select__item:hover, .select__item:focus {
  color: var(--white);
  background-color: var(--red);
}

.intro {
  color: var(--white);
  text-align: center;
  padding-top: 40px;
  padding-bottom: 40px;
  background-color: var(--red);
  background-image: url("../img/icons/safari.svg");
  background-repeat: no-repeat;
  background-position: center 150px;
  background-size: calc(100vw - 30px);
  position: relative;
}
@media (min-width: 768px) {
  .intro {
    background-size: calc(100vw - 60px);
  }
}
@media (min-width: 992px) {
  .intro {
    background-size: calc(100vw - 120px);
  }
}
@media (min-width: 1440px) {
  .intro {
    background-size: auto;
  }
}

.intro__container {
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .intro__container {
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media (min-width: 1440px) {
  .intro__container {
    padding-left: 50px;
    padding-right: 50px;
  }
}

.intro__slider {
  margin-left: auto;
  margin-right: auto;
  overflow: hidden;
  position: relative;
  z-index: 1;
}

.intro__slider-wrapper {
  display: flex;
  max-width: 90vw;
  height: auto !important;
  position: relative;
  z-index: 1;
}

.intro__slide {
  flex-shrink: 0;
  height: 100%;
  max-height: 850px;
  cursor: grab;
  overflow: hidden;
  position: relative;
  transition-property: transform;
}

.intro__img {
  margin-left: auto;
  margin-right: auto;
}

.intro__pagination {
  margin-top: 30px;
  position: static;
}

.intro__pagination-bullet {
  display: inline-block;
  width: 16px;
  height: 16px;
  margin-right: 25px;
  background-color: transparent;
  border: 1px solid var(--white);
  border-radius: 50%;
  cursor: pointer;
  opacity: 1;
}
.intro__pagination-bullet:last-child {
  margin-right: 0;
}
.intro__pagination-bullet--active {
  background-color: var(--white);
}

.intro__scroll {
  display: inline-flex;
  align-items: center;
  flex-direction: column;
  margin-top: 30px;
}
@media (min-width: 1440px) {
  .intro__scroll {
    margin-top: 60px;
  }
}

.intro__scroll-text {
  font-size: 30px;
  font-weight: 600;
}
@media (max-width: 767px) {
  .intro__scroll-text {
    display: none;
  }
}

.intro__scroll-icon {
  margin-top: 15px;
}
@media (max-width: 767px) {
  .intro__scroll-icon {
    margin-top: 0;
  }
}

.catalog {
  padding-top: 60px;
  padding-bottom: 120px;
}

.catalog__container {
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .catalog__container {
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media (min-width: 1440px) {
  .catalog__container {
    padding-left: 50px;
    padding-right: 50px;
  }
}

.catalog__title {
  font-size: 40px;
  font-weight: 700;
  text-align: center;
  padding-left: 10px;
  padding-right: 10px;
}
.catalog__title::before, .catalog__title::after {
  display: none;
  content: "";
  height: 1px;
  flex-grow: 1;
  flex-shrink: 0;
  background-color: var(--black);
}
.catalog__title::before {
  margin-right: 110px;
}
.catalog__title::after {
  margin-left: 110px;
}
@media (min-width: 1440px) {
  .catalog__title {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .catalog__title::before, .catalog__title::after {
    display: block;
  }
}

.catalog__text {
  font-size: 24px;
  text-align: center;
  max-width: 1100px;
  margin-left: auto;
  margin-right: auto;
}

.catalog__list {
  list-style: none;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(290px, 1fr));
  gap: 60px 50px;
  margin-top: 60px;
}

.card {
  text-align: center;
  transition: 0.2s;
  position: relative;
}
.card:hover, .card:focus-within {
  box-shadow: var(--shadow);
}
.card:hover .card__img, .card:focus-within .card__img {
  opacity: 0.75;
}
.card:hover .card__buttons, .card:focus-within .card__buttons {
  opacity: 1;
}

.card__overlay {
  height: 457px;
  background-color: var(--black);
  position: relative;
}

.card__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: 0.2s;
}

.card__buttons {
  text-align: center;
  opacity: 0;
  position: absolute;
  top: 160px;
  left: 50%;
  transform: translateX(-50%);
  transition: 0.2s;
}

.card__like {
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
  color: var(--red);
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 35px;
  height: 35px;
  margin-bottom: 15px;
  background-color: var(--white);
}
.card__like:hover .card__like-icon, .card__like:focus .card__like-icon {
  fill: var(--red);
}

.card__add {
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  font-size: 10px;
  font-weight: 700;
