.orders__buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  margin-top: 30px;
}
@media (max-width: 767px) {
  .orders__buttons {
    gap: 15px;
  }
}

.orders__button {
  color: var(--white);
  font-size: 1.125rem;
  font-weight: 800;
  letter-spacing: 2px;
  text-align: center;
  text-transform: uppercase;
  padding: 10px 15px;
  background-color: var(--red);
  border: 1px solid var(--red);
  border-radius: 6px;
  transition: 0.2s;
  overflow: hidden;
  cursor: pointer;
  z-index: 1;
}
.orders__button--invert {
  color: var(--black);
  background-color: var(--white);
}
.orders__button:hover, .orders__button:focus-visible {
  color: var(--white);
  background-color: var(--orange);
  border-color: transparent;
}
@media (min-width: 768px) {
  .orders__button {
    padding-right: 30px;
    padding-left: 30px;
  }
}
@media (max-width: 991px) {
  .orders__button {
    flex-grow: 1;
  }
}

.favorites__list {
  list-style: none;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(calc(320px - (15px * 2)), 1fr));
  gap: 30px;
}

.favorites__item {
  padding: calc(1.3125rem + 0.75vw);
  display: grid;
  box-shadow: var(--shadow-2);
}
@media (min-width: 1200px) {
  .favorites__item {
    padding: 1.875rem;
  }
}

.favorites__item-inner {
  margin-bottom: calc(1.5rem + 3vw);
  display: flex;
  align-items: flex-start;
}
@media (min-width: 1200px) {
  .favorites__item-inner {
    margin-bottom: 3.75rem;
  }
}

.favorites__item-link {
  flex-shrink: 0;
  margin-right: 30px;
}
@media (max-width: 991px) {
  .favorites__item-link {
    margin-right: 15px;
  }
}

.favorites__item-img {
  width: 86px;
  height: 86px;
  object-fit: cover;
}

.favorites__item-title {
  font-size: 20px;
  font-weight: 600;
  letter-spacing: 0.2px;
  margin-bottom: 5px;
  transition: 0.2s;
}
.favorites__item-title:hover, .favorites__item-title:focus {
  color: var(--red);
}

.favorites__item-size {
  color: var(--dark-grey);
  font-size: 16px;
  letter-spacing: 0.2px;
  margin-bottom: 8px;
}

.favorites__item-price {
  color: var(--red);
  font-family: var(--sub-font);
  font-weight: 400;
}

.favorites__item-buttons {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 15px;
  margin-top: auto;
}
@media (min-width: 768px) {
  .favorites__item-buttons {
    column-gap: 30px;
  }
}
@media (min-width: 1440px) {
  .favorites__item-buttons {
    column-gap: 60px;
  }
}

.favorites__button {
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
  text-transform: uppercase;
  flex-grow: 1;
  padding: 10px 15px;
  transition: 0.2s;
}
.favorites__button--buy {
  color: var(--white);
  font-size: 1.125rem;
  font-weight: 800;
  letter-spacing: 2px;
  padding: 10px 30px;
  text-transform: uppercase;
  background-color: var(--red);
  border: 1px solid var(--red);
  border-radius: 6px;
  box-sizing: border-box;
  transition: 0.2s;
  cursor: pointer;
  padding: 10px 15px;
}
.favorites__button--buy:hover, .favorites__button--buy:focus {
  color: var(--black);
  background-color: var(--white);
}
.favorites__button--remove {
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--red);
  font-weight: 600;
  letter-spacing: 0.2px;
}
.favorites__button--remove:hover, .favorites__button--remove:focus {
  color: var(--orange);
}

.favorites__button-icon {
  margin-right: 8px;
}

.address-book__list {
  list-style: none;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  margin-bottom: 30px;
}

.address-book__item {
  padding: calc(1.3125rem + 0.75vw) calc(1.3125rem + 0.75vw) calc(1.3125rem + 0.75vw) calc(1.375rem + 1.5vw);
  box-shadow: var(--shadow-2);
}
@media (min-width: 1200px) {
  .address-book__item {
    padding: 1.875rem 1.875rem 1.875rem 2.5rem;
  }
}

.address-book__address {
  letter-spacing: 0.2px;
}

.address-book__title {
  font-size: 1.25rem;
  font-weight: 600;
  margin-bottom: 15px;
}

.address-book__text:last-child {
  margin-bottom: 0;
}

.address-book__item-buttons {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-top: 15px;
}

.address-book__item-button {
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
  margin-right: calc(1.525rem + 3.3vw);
  color: var(--red);
  font-weight: 700;
  letter-spacing: 0.2px;
  transition: 0.2s;
}
@media (min-width: 1200px) {
  .address-book__item-button {
    margin-right: 4rem;
  }
}
.address-book__item-button:last-child {
  margin-right: 0;
}
.address-book__item-button:hover, .address-book__item-button:focus {
  color: var(--orange);
}

.address-book__button {
  color: var(--white);
  font-size: 1.125rem;
  font-weight: 800;
  letter-spacing: 2px;
  padding: 10px 30px;
  text-transform: uppercase;
  background-color: var(--red);
  border: 1px solid var(--red);
  border-radius: 6px;
  box-sizing: border-box;
  transition: 0.2s;
  cursor: pointer;
}
.address-book__button:hover, .address-book__button:focus {
  color: var(--black);
  background-color: var(--white);
}
@media (max-width: 767px) {
  .address-book__button {
    display: block;
    min-width: 250px;
    margin-left: auto;
    margin-right: auto;
  }
}

.account__form {
  padding: calc(1.5rem + 3vw) calc(1.525rem + 3.3vw) calc(1.405rem + 1.86vw);
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 30px;
  box-shadow: var(--shadow-2);
}
@media (min-width: 1200px) {
  .account__form {
    padding: 3.75rem 4rem 2.8rem;
  }
}

.account__form-col {
  flex-grow: 1;
}
@media (min-width: 1440px) {
  .account__form-col {
    max-width: 350px;
  }
}

.account__button {
  color: var(--white);
  font-size: 1.125rem;
  font-weight: 800;
  letter-spacing: 2px;
  padding: 10px 30px;
  text-transform: uppercase;
  background-color: var(--red);
  border: 1px solid var(--red);
  border-radius: 6px;
  box-sizing: border-box;
  transition: 0.2s;
  cursor: pointer;
  margin-top: 30px;
}
.account__button:hover, .account__button:focus {
  color: var(--black);
  background-color: var(--white);
}
@media (max-width: 767px) {
  .account__button {
    display: block;
    min-width: 250px;
    margin-left: auto;
    margin-right: auto;
  }
}

.scroll-top {
  display: block;
  width: 60px;
  height: 50px;
  background-image: url("../img/icons/arrow-top.svg");
  background-repeat: no-repeat;
  background-position: center;
  border: 1px solid var(--black);
  border-radius: 4px;
  box-sizing: border-box;
  position: fixed;
  right: 0;
  bottom: 30px;
  transform: translateX(100%);
  transition: 0.3s ease-in-out;
  z-index: 9;
}
.scroll-top--active {
  right: 50px;
  transform: translateX(0);
}
@media (max-width: 991px) {
  .scroll-top {
    width: 50px;
    height: 40px;
  }
  .scroll-top--active {
    right: 15px;
    bottom: 70px;
  }
}