.account__form {
  @include padding(3.75rem 4rem 2.8rem);

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 30px;
  box-shadow: var(--shadow-2);
}

.account__form-col {
  flex-grow: 1;

  @include media-min(xl) {
    max-width: 350px;
  }
}

.account__button {
  @include button-secondary;

  margin-top: 30px;

  @include media-max(md) {
    display: block;
    min-width: 250px;
    margin-left: auto;
    margin-right: auto;
  }
}
