.product-page {
  @include padding-top(5.3rem);
  @include padding-bottom(7.5rem);
}

.product-page__container {
  @include container;
}

.product-page__title {
  @include margin-bottom(2.5rem);

  font-size: 1.875rem;
  font-weight: 800;
  letter-spacing: 3px;
  text-transform: uppercase;
}

.product-page__inner {
  display: grid;
  grid-template-columns: 1fr;

  @include media-min(md) {
    grid-template-columns: 300px 1fr;
    column-gap: 20px;
  }
  @include media-min(xl) {
    column-gap: 85px;
  }
}

.product-page__header {
  display: flex;
  justify-content: flex-end;
  border-bottom: 1px solid var(--black-5);
  padding-bottom: 15px;
}

.product-page__list {
  @include reset-list;

  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(calc(map-get($widths, sm) - (#{$gap-sm} * 2)), 1fr));
  gap: 100px 30px;
  margin-top: 30px;
}
