:root {
  --white: #fff;
  --black: #000;
  --black-25: rgba(0,0,0, .25);
  --black-5: rgba(0,0,0, .5);
  --grey: #f5f5f5;
  --grey-2: #c4c4c4;
  --grey-3: #e1e1e1;
  --dark-grey: #686868;
  --red: #ed165f;
  --orange: #ff5318;
  --orange-light: #F07B4E;
  --green: #08B727;

  --shadow: 0 0 50px rgba(0,0,0, .1);
  --shadow-2: 0 0 5px 1px rgba(0,0,0, .1);

  --main-font: "Mulish", sans-serif;
  --sub-font: "Roboto", sans-serif;
}

html {
  color: var(--black);
  font-family: var(--main-font);
	font-size: 16px;
  line-height: 1.2;
  font-weight: 400;
	height: 100%;
  scroll-behavior: smooth;
}

body {
	display: grid;
	grid-template-rows: min-content 1fr min-content;
	align-content: start;
	min-height: 100%;
}

img {
  display: block;
	max-width: 100%;
	height: auto;
}

a {
	color: inherit;
	text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-top: 0;
}

input {
  appearance: none;
}

input:disabled + label {
  opacity: .4;
}

input[type=number] { -moz-appearance: textfield; }
input[type=number]::-webkit-outer-spin-button,
input[type=number]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

main {
  position: relative;
}

.uppercase {
  text-transform: uppercase;
}

.overflow-hidden {
  overflow: hidden;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
